import Vue from "vue";
import Router from "vue-router";

// Import Components for Routing:
import patientManagement from "@/components/PatientManagement.vue";
import stats from "@/components/Statistics.vue";
import main from "@/components/MainComponent.vue";

Vue.use(Router);

const routes = [
  {
    path: "/patients",
    component: patientManagement,
    meta: {
      auth: true,
      title: "seca Demo EMR - Patient Management"
    }
  },
  {
    path: "/stats",
    component: stats,
    meta: {
      auth: true,
      title: "seca Demo EMR - Data Analysis"
    }
  },
  {
    path: "/",
    component: main,

    meta: {
      auth: true,
      title: "seca Demo EMR"
    }
  }
];

export default new Router({
  routes
});
