import axios from "axios";

const state = {
  patients: []
};

const getters = {
  allPatients: state => state.patients
};

const actions = {
  async fetchPatients({ commit }) {
    const response = await axios.get("http://"+window.location.host+"/measure/patients");
    //const response = await axios.get("http://localhost:9006/measure/patients");
    commit("setPatients", response.data);
  }
};

const mutations = {
  setPatients: (state, patients) => (state.patients = patients)
};

export default {
  state,
  getters,
  actions,
  mutations
};
