 <template>
  <div class="pa-2">
    <v-card class="px-2" :flat="true" elevation="0.5">
      <div style="margin-right:6px; margin-left:6px">
        <table id="secondTable" class="container-fluid">
          <thead>
            <tr>
              <th>Patient ID</th>
              <th>Type</th>
              <th>Value</th>
              <th>Unit</th>
              <th>Date</th>
              <th>User ID</th>
            </tr>
          </thead>
          <tbody>
            <tr
              :key="item._id"
              v-for="item in filteredItems"
              @click="showBundle(item)"
              :class="rowClass(item)"
              @dblclick="showHL7Dialog(item)"
            >
              <td v-if="item.patient">{{item.patient.patientId}}</td>
              <td v-else></td>
              <td>{{item.type}}</td>
              <td>{{item.value}}</td>
              <td>{{item.unit}}</td>
              <td>{{item.date | moment("subtract","1 hour","dddd, MMMM Do YYYY, H:mm:ss")}}</td>
              <td>{{item.user.userId}}</td>
            </tr>
          </tbody>
        </table>
      </div>

      <v-dialog v-model="dialog">
        <v-card>
          <v-card-title class="headline grey lighten-2" primary-title>
            <span class="headline">HL7 Message</span>
          </v-card-title>
          <v-list dense>
            <v-list-item-group color="primary">
              <v-list-item v-for="msg in message" :key="msg.Index">
                <span :key="list.id" v-for="(list, index ) in msg.Segment">
                  <span v-if="index === 0" style="font-weight:bolder;color:green;">{{list}}</span>

                  <span v-else-if="index != 0" style="font-weight:bold;">{{list}}</span>
                  <span
                    v-if="index != msg.Segment.length-1"
                    style="font-weight:bolder;color:blue;"
                  >|</span>
                </span>
              </v-list-item>
            </v-list-item-group>
          </v-list>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialog = false">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-card>
  </div>
</template>
 
<script>
export default {
  data() {
    return {
      rowNumber: "",
      bundleId: "",
      dialog: false,

      message: [
        {
          Index: "",
          Type: "",
          Segment: ""
        }
      ],
      fields: [
        {
          key: "patient",
          sortable: false,
          label: "Patient ID",
          formatter: this.getPatientId
        },
        { key: "type", sortable: false, label: "Type" },
        { key: "value", sortable: false, label: "Value" },
        { key: "unit", sortable: false, label: "Unit" },
        { key: "date", sortable: false, label: "Time" },
        {
          key: "user",
          sortable: false,
          label: "User ID",
          formatter: this.getUserId
        }
      ]
    };
  },
  watch: {
    resultValues: function() {
      this.bundleId = this.currentSelectedBundle;
    }
  },

  computed: {
    filteredItems: function() {
      return this.resultValues.slice(0, this.perPage);
    }
  },
  methods: {
    rowClass(item) {
      if (this.bundleId == item.measurement_bundle_id) {
        return "highlight";
      } else {
        return "highlightUnSelected";
      }
    },
    showBundle(item) {
      
      this.bundleId = item.measurement_bundle_id;
      this.$store.commit("setSelectedBundle", item.measurement_bundle_id);
    },
    getPatientId(value) {
      {
        return `${value.patientId}`;
      }
    },
    getUserId(value) {
      return `${value.userId}`;
    },
    showHL7Dialog(item) {
      this.message = [];
      var self = this;

      var hl7 = require("simple-hl7");
      this.dialog = true;
      var hl7message = item.message;

      var parser = new hl7.Parser();
      hl7message = parser.parse(hl7message);

      var segments = hl7message.segments;
      var header = hl7message.header;

      self.message.push({
        Index: 0,
        Segment: parser
          .parseHeader(header.toString())
          .toString()
          .split("|"),
        Type: header.name.toString()
      });

      for (var i = 0; i < segments.length; i++) {
        var returnString = segments[i].toString(header.delimiters).split("|");

        self.message.push({
          Index: i + 1,
          Segment: returnString,
          Type: segments[i].name.toString()
        });

        if (i != segments.length - 1) {
          returnString = header.delimiters.segmentSeperator;
        }
      }
    }
  },
  name: "ResultTable",
  props: {
    resultValues: Array,
    perPage: Number,
    currentSelectedBundle: String
  }
};
</script>

<style scoped>
.table {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  border-collapse: collapse;
  border-spacing: 1;
}

.highlight {
  border-right: 3px rgb(192, 187, 187, 0.5) solid;
  border-left: 5px solid #4caf50;
  background-color: rgb(192, 187, 187, 0.05);
}

.highlightUnSelected {
  border-left: 5px solid rgb(255, 255, 255);
}

table tbody {
  border-right: 5px solid white;
}

th {
  padding: 0.5em;
  position: relative;
  border-bottom: 3px rgb(192, 187, 187, 0.2) solid;
  flex-grow: 0;
}

td {
  padding: 1em;
  position: relative;
  border-bottom: 1px rgb(192, 187, 187, 0.2) solid;
  flex-grow: 0;
}

td::after {
  content: "";
  background-color: rgb(192, 187, 187, 0.2);
  position: absolute;
  left: 100%;
  top: 6px;
  bottom: 6px;
  width: 1px;
}

td:last-child::after {
  border-right: 2px #ffffff solid;
}

th::after {
  content: "";
  background-color: rgba(26, 26, 26, 0.2);
  position: absolute;
  left: 100%;
  top: 6px;
  bottom: 6px;
  width: 1px;
}

th:last-child::after {
  border-right: 2px #ffffff solid;
}
</style>