import axios from "axios";

const state = {
  resultValues: [],
  selectedBundle: null
};

const getters = {
  allResults: state => state.resultValues,
  currentSelectedBundle: state => state.selectedBundle
};

const actions = {
  async fetchResultValues({ commit }) {
    const response = await axios.get(
      "http://"+window.location.host+"/measure/last100measurements"
    );
    //console.log("Address: "+window.location.host)
    //const response = await axios.get(
    //  "http://localhost:9006/measure/last100measurements"
    //);
    const sortedResults = response.data.sort((a, b) => new Date(b.date) - new Date(a.date))
    
    commit("setResultValues", sortedResults);
    commit("setSelectedBundle", sortedResults[0].measurement_bundle_id);
  },

  setSelectedBundle(value) {
    commit("setSelectedBundle", value);
  }
};

const mutations = {
  setResultValues: (state, resultValues) => (state.resultValues = resultValues),
  setSelectedBundle: (state, selectedBundle) =>
    (state.selectedBundle = selectedBundle)
};

export default {
  state,
  getters,
  actions,
  mutations
};
