 <template>
  <v-app>
    <v-app-bar color="#191919" max-height="60px" app>
      <v-btn text large @click.stop="drawer = !drawer">
        <i class="fas fa-list-old-style pa-0 ma-0" style="color:#ED252A; font-size: 120%;"></i>
      </v-btn>
      <v-divider class="ma-2" vertical inset></v-divider>
      <div>
        <span class="white--text" style=" font-size:large">Demo System</span>
      </div>
      <v-spacer class="ma-2"></v-spacer>

      <div>
        <v-badge color="green" overlap>
          <template v-slot:badge>
            <span v-if="incomingCounter.length>0">{{ incomingCounter.length }}</span>
          </template>
          <v-icon v-if="incomingCounter.length>0" color="#706e6e" large>mdi-email</v-icon>
        </v-badge>
      </div>
      <v-divider class="ma-2" vertical inset></v-divider>
      <div>
        <span class="white--text">{{UserLabel}}:</span>
        <span
          v-if="getSelectedElement().user"
          class="white--text"
          style=" font-size:large"
        >{{getSelectedElement().user.userId}}</span>
        <v-divider class="ma-2" vertical inset></v-divider>
        <font-awesome-icon :icon="['far', 'user-circle']" size="2x" style="color:#706e6e" />
      </div>
    </v-app-bar>

    <v-content>
      <v-container fluid>
        <v-layout row justify-space-between>
          <v-flex xl6 md6 lg6>
            <patientBox
              v-if="getSelectedElement().patient"
              :patientValues="getSelectedElement().patient"
              :label="PatientLabel"
            ></patientBox>
          </v-flex>
          <v-flex xl6 md6 lg6>
            <deviceBox
              v-if="getSelectedElement().device"
              :deviceValues="getSelectedElement().device"
            ></deviceBox>
          </v-flex>
          <v-flex xl6 md6 lg6>
            <weightValueBox :measurementValues="getSelectedElementBundle('Weight')"></weightValueBox>
          </v-flex>
          <v-flex xl6 md6 lg6>
            <heightValueBox :measurementValues="getSelectedElementBundle('Height')"></heightValueBox>
          </v-flex>
          <v-flex xl3 md3 lg3>
            <bloodPressureValueBox
              :measurementValuesSys="getSelectedElementBundle('NBPs')"
              :measurementValuesDia="getSelectedElementBundle('NBPd')"
            ></bloodPressureValueBox>
          </v-flex>
          <v-flex xl3 md3 lg3>
            <temperaturValueBox :measurementValues="getSelectedElementBundle('TempInEar')"></temperaturValueBox>
          </v-flex>
          <v-flex xl3 md3 lg3>
            <pulseValueBox :measurementValues="getSelectedElementBundle('PulseNiBP')"></pulseValueBox>
          </v-flex>
          <v-flex xl3 md3 lg3>
            <oxygenValueBox :measurementValues="getSelectedElementBundle('SpO2')"></oxygenValueBox>
          </v-flex>

          <v-flex xl12 md12 lg12>
            <resultTable
              :perPage="perPage"
              :resultValues="allResults"
              :currentSelectedBundle="currentSelectedBundle"
            ></resultTable>
          </v-flex>
          <v-spacer></v-spacer>
        </v-layout>
      </v-container>
    </v-content>

    <v-navigation-drawer width="400" v-model="drawer" absolute temporary dark>
      <v-toolbar flat>
        <v-app-bar-nav-icon></v-app-bar-nav-icon>
        <v-list-tile-title class="title" color="#191919">Settings</v-list-tile-title>
      </v-toolbar>

      <v-row justify="center">
        <v-expansion-panels popout>
          <v-expansion-panel>
            <v-expansion-panel-header>
              Notation
              <template v-slot:actions>
                <v-icon color="error">mdi-pencil-outline</v-icon>
              </template>
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-text-field
                error
                outline
                hint="Define your Patient / Member Label"
                label="Patient Identifier"
                value="Patient"
                v-model="PatientLabel"
              ></v-text-field>
              <v-text-field
                error
                outline
                hint="Define your User / Trainer Label"
                label="User Identifier"
                value="User"
                v-model="UserLabel"
              ></v-text-field>
            </v-expansion-panel-content>
          </v-expansion-panel>

          <v-expansion-panel>
            <v-expansion-panel-header>
              Measurement Table
              <template v-slot:actions>
                <v-icon color="error">mdi-table</v-icon>
              </template>
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-select
                v-model="perPage"
                :items="pageOptions"
                attach
                label="Table Results Per Page"
                solo
              ></v-select>
            </v-expansion-panel-content>
          </v-expansion-panel>
          <v-expansion-panel>
            <v-expansion-panel-header>
              Barcodes
              <template v-slot:actions>
                <v-icon color="error">mdi-barcode</v-icon>
              </template>
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-text-field v-model="uidBarcode" label="User ID" maxlength="22" counter error></v-text-field>
              <barcode
                v-bind:value="uidBarcode"
                :options="{ displayValue: false ,height: '40' , width:'1', format: 'CODE128' } "
              ></barcode>
              <v-divider></v-divider>
              <v-text-field v-model="pidBarcode" label="Patient ID" maxlength="22" counter error></v-text-field>
              <barcode
                v-model="pidBarcode"
                :options="{ displayValue: false , height: '40', width:'1', format: 'CODE128' }"
              ></barcode>
            </v-expansion-panel-content>
          </v-expansion-panel>
          <v-expansion-panel @click="isFetchResults= !isFetchResults">
            <v-expansion-panel-header v-if="isFetchResults" disable-icon-rotate>
              Pause Fetching
              <template v-slot:actions>
                <v-icon color="error">mdi-pause</v-icon>
              </template>
            </v-expansion-panel-header>
            <v-expansion-panel-header v-else disable-icon-rotate>
              Get latest measurements
              <template v-slot:actions>
                <v-icon color="error">mdi-play</v-icon>
              </template>
            </v-expansion-panel-header>
            <v-expansion-panel-content></v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-row>
    </v-navigation-drawer>
  </v-app>
</template>
 
<script>
import { mapGetters, mapActions } from "vuex";

import heightValueBox from "@/components/HeightValueBox.vue";
import weightValueBox from "@/components/WeightValueBox.vue";

import oxygenValueBox from "@/components/OxygenValueBox.vue";
import pulseValueBox from "@/components/PulseValueBox.vue";
import bloodPressureValueBox from "@/components/BloodPressureValueBox.vue";
import temperaturValueBox from "@/components/TemperaturValueBox.vue";

import resultTable from "@/components/ResultTable.vue";
import deviceBox from "@/components/DeviceBox.vue";
import patientBox from "@/components/PatientBox.vue";

import io from "socket.io-client";

export default {
  components: {
    resultTable,
    deviceBox,
    patientBox,
    weightValueBox,
    heightValueBox,
    oxygenValueBox,
    pulseValueBox,
    bloodPressureValueBox,
    temperaturValueBox
  },
  data() {
    return {
      UserLabel: "User",
      PatientLabel: "Patient",
      drawer: null,
      pageOptions: [
        3,
        4,
        5,
        6,
        7,
        8,
        9,
        10,
        15,
        20,
        25,
        30,
        40,
        50,
        60,
        80,
        100
      ],
      perPage: 20,
      pidBarcode: "123456789 Max Muster",
      uidBarcode: "U2345 Doctor Demo",
      snackbar: false,
      snackbarColor: "success",
      timeout: 6000,
      newPatientId: "",
      socket: io(),
      isFetchResults: true,
      incomingCounter: []
    };
  },

  created() {
    this.fetchResultValues();
  },
  computed: mapGetters(["allResults", "currentSelectedBundle"]),

  notifications: {
    showSuccessMsg: {
      type: "success",
      title: "New Measurement",
      message: "Patient Id: ",
      position: "topCenter"
    }
  },

  watch: {
    isFetchResults: function() {
      if (this.isFetchResults) {
        this.fetchResultValues();

        this.incomingCounter.forEach(element => {
          this.showSuccessMsg({
            title: "Measurement received",
            message: "Patient Id: " + element
          });
      this.incomingCounter =[]


        });
      } else {
        this.showSuccessMsg({
          title: "Pause Receiving",
          message: ""
        });
      }
    }
  },

  methods: {
    ...mapActions(["fetchResultValues"]),
    showNot() {
      this.showSuccessMsg({
        title: "my title",
        message: "just an error",
        position: "topCenter",
        transitionIn: "fadeInDown"
      });
    },

    getSelectedElement() {
      var currentValues = this.allResults.filter(
        element => element.measurement_bundle_id === this.currentSelectedBundle
      );
      if (currentValues.length >= 1) {
        return currentValues[0];
      } else {
        return currentValues;
      }
    },

    getSelectedElementBundle(parameter) {
      var currentValues = this.allResults.filter(
        element =>
          element.measurement_bundle_id === this.currentSelectedBundle &&
          element.type === parameter
      );
      return currentValues[0];
    }
  },
  mounted: function() {
    this.socket.on("MESSAGE", data => {
      if (this.isFetchResults) {
        this.fetchResultValues().then(vm.$forceUpdate());
        this.newPatientId = data;
        this.showSuccessMsg({
          title: "Measurement received",
          message: "Patient Id: " + data
        });
      } else {
        this.incomingCounter.push(data);
      }
    });
  }
};
</script>

<style scoped >
.container {
  max-width: 2000px;
}



.pause {
  opacity: 0.5;
}
</style>

<style>

::-webkit-scrollbar {
  width: 0em;
  background: transparent;
  display: inline !important;
}
.my-style {
  display: block;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-align: left;
  font-size: 18px;
  padding: 10px;
  margin: 0 5px 5px;
  color: #fff;
  background: rgba(76, 175, 79, 0.73);
  border-left: 4px solid #107514;
}
.my-style .notification-title {
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
}
.my-style .notification-content {
  font-size: 17px;
  font-style: normal;
  font-weight: lighter;
}
</style>