<template>
  <v-app>
    <v-app-bar color="#191919" max-height="60px" app>
      <i class="fas fa-list-old-style pa-0 ma-0" style="color:#ED252A; font-size: 120%;"></i>

      <v-divider class="ma-2" vertical inset></v-divider>
      <div>
        <span class="white--text" style=" font-size:large">Demo System</span>
      </div>
      <v-spacer class="ma-2"></v-spacer>
    </v-app-bar>
    <v-content>
      <v-layout row wrap>
        <v-flex xl6 lg12 md12 sm12>
          <div class="pa-2 ">
            <v-card elevation="6">
              <div class="pa-4">
                <chart :options="chartOptionsBar"></chart>
              </div>
              <div>
                <v-chip class="ma-2" color="primary" outlined pill>
                  Total Number of Weight Measures: {{this.results.filter(item => item.type == "Weight").length}}
                  <v-icon right>mdi-weight-kilogram</v-icon>
                </v-chip>
                <v-chip class="ma-2" color="primary" outlined pill>
                  Total Number of Measures: {{this.results.length}}
                  <v-icon right>mdi-weight</v-icon>
                </v-chip>
              </div>
            </v-card>
          </div>
        </v-flex>

        <v-flex xl6 lg12 md12 sm12>
          <div class="pa-2">
            <v-card elevation="6">
              <div class="pa-4">
                <chart :options="chartOptionsBarHeight"></chart>
              </div>
              <div align-center>
                <v-chip class="ma-2" color="primary" outlined pill>
                  Total Number of Height Measures: {{this.results.filter(item => item.type == "Height").length}}
                  <v-icon right>mdi-human-male-height-variant</v-icon>
                </v-chip>
                <v-chip class="ma-2" color="primary" outlined pill>
                  Total Number of Measures: {{this.results.length}}
                  <v-icon right>mdi-weight</v-icon>
                </v-chip>
              </div>
            </v-card>
          </div>
        </v-flex>

        <v-flex xl6 lg12 md12 sm12 >
          <div class="pa-2">
            <v-card elevation="6">
              <div class="pa-4">
                <chart :options="chartOptionsBarTimeFrame"></chart>
              </div>
              <div align-center>
                <v-chip
                  class="ma-2"
                  color="primary"
                  outlined
                  pill
                >Total Number of Selected Measures: {{this.timeArray.filter(item => valuetype).length}}</v-chip>
              </div>

              <v-chip class="ma-2" color="primary" outlined pill>
                Total Number of Measures: {{this.results.length}}
                <v-icon right>mdi-weight</v-icon>
              </v-chip>
              <v-divider class="mx-4"></v-divider>

              <v-card-text>
                <v-row>
                  <v-col cols="12" lg="6">
                    <v-menu v-model="menu1" :close-on-content-click="false" max-width="290">
                      <template v-slot:activator="{ on }">
                        <v-text-field
                          :value="computedDateFormattedMomentstart"
                          clearable
                          label="Start Date"
                          readonly
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker
                        v-model="startdate"
                        @change="menu1 = false"
                        @click:date="pickerDate()"
                      ></v-date-picker>
                    </v-menu>
                  </v-col>

                  <v-col cols="12" lg="6">
                    <v-menu v-model="menu2" :close-on-content-click="false" max-width="290">
                      <template v-slot:activator="{ on }">
                        <v-text-field
                          :value="computedDateFormattedMomentend"
                          clearable
                          label="End Date"
                          readonly
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker
                        v-model="enddate"
                        @change="menu2 = false"
                        @click:date="pickerDate()"
                      ></v-date-picker>
                    </v-menu>
                  </v-col>
                </v-row>
              </v-card-text>

              <v-divider class="mx-4"></v-divider>

              <v-card-actions>
                <v-select
                  :items="items"
                  label="Value"
                  outlined
                  v-model="valuetype"
                  @change="pickerDate()"
                  multiple
                  chips
                ></v-select>
              </v-card-actions>
            </v-card>
          </div>
        </v-flex>

        <v-flex xl6 lg12 md12 sm12>
          <div class="pa-2">
            <v-card elevation="6">
              <div class="pl-8 ml-8">
                <chart :options="chartOptionsBarUser"></chart>
              </div>
            </v-card>
          </div>
        </v-flex>
      </v-layout>
    </v-content>
  </v-app>
</template>
<script>
const axios = require("axios").default;
import * as d3 from "d3-array";
import moment from "moment";

export default {
  data() {
    return {
      startdate: new Date(new Date() - 5 * 24 * 3600 * 1000)
        .toISOString()
        .substr(0, 10),
      enddate: new Date().toISOString().substr(0, 10),
      menu1: false,
      menu2: false,
      results: [],
      test: [],
      graph: [],
      graphHeight: [],
      valuetype: ["Weight"],

      items: [
        "Weight",
        "Height",
        "Perfusion Index",
        "SpO2",
        "TempInEar",
        "NBPm",
        "NBPd",
        "NBPs",
        "PulseNiBP"
      ],
      timeArray: [],

      chartOptionsBarUser: {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },

        xAxis: {
          type: "value",
          boundaryGap: [0, 0.01]
        },
        yAxis: {
          type: "category",
          name: "# of measures"
        },
        series: [
          {
            type: "bar",
            data: []
          }
        ],

        title: {
          text: "Measures per User",
          x: "center",
          textStyle: {
            fontSize: 18
          }
        },
        color: ["#127ac2"]
      },

      chartOptionsBarTimeFrame: {
        tooltip: {
          trigger: "axis"
        },

        xAxis: {
          data: [
            0,
            60,
            120,
            180,
            240,
            300,
            360,
            420,
            480,
            540,
            600,
            660,
            720,
            780,
            840,
            900,
            1080
          ],
          axisTick: {
            alignWithLabel: true
          },
          axisLabel: {
            formatter: "{value} h"
          },
          name: "Time"
        },
        yAxis: {
          name: "Number of measures",
          splitLine: { show: false }
        },
        series: [
          {
            type: "bar",
            data: []
          }
        ],

        title: {
          text: "Total Measures Histogram within selected Timeframe",
          x: "center",
          textStyle: {
            fontSize: 18
          }
        },
        color: ["#127ac2"]
      },

      chartOptionsBar: {
        tooltip: {
          trigger: "axis"
        },

        xAxis: {
          data: [
            0,
            60,
            120,
            180,
            240,
            300,
            360,
            420,
            480,
            540,
            600,
            660,
            720,
            780,
            840,
            900,
            1080
          ],
          axisTick: {
            alignWithLabel: true
          },
          axisLabel: {
            formatter: "{value} h"
          },
          name: "Time"
        },
        yAxis: {
          name: "Number of measures",
          splitLine: { show: false }
        },
        series: [
          {
            type: "bar",
            data: []
          }
        ],

        title: {
          text: "Total Weight Measures",
          x: "center",
          textStyle: {
            fontSize: 18
          }
        },
        color: ["#127ac2"]
      },

      chartOptionsBarHeight: {
        tooltip: {
          trigger: "axis"
        },

        xAxis: {
          data: [
            0,
            60,
            120,
            180,
            240,
            300,
            360,
            420,
            480,
            540,
            600,
            660,
            720,
            780,
            840,
            900,
            1080
          ],
          axisTick: {
            alignWithLabel: true
          },
          axisLabel: {
            formatter: "{value} h"
          },
          name: "Time"
        },
        yAxis: {
          name: "Number of measures",
          splitLine: { show: false }
        },
        series: [
          {
            type: "bar",
            data: []
          }
        ],

        title: {
          text: "Total Height Measures",
          x: "center",
          textStyle: {
            fontSize: 18
          }
        },
        color: ["#127ac2"]
      }
    };
  },

  computed: {
    computedDateFormattedMomentstart() {
      return this.startdate ? moment(this.startdate) : "";
    },

    computedDateFormattedMomentend() {
      return this.enddate ? moment(this.enddate) : "";
    }
  },
  beforeRouteEnter(to, from, next) {
    axios.get("http://"+window.location.host+"/measure/measurements").then(response => {
      next(vm => (vm.results = response.data));
    });
  },

  watch: {
    results: "manipulateData"
  },

  methods: {
    pickerDate() {
      this.timeArray = this.results.filter(
        a =>
          moment(a.date) > moment(this.startdate) &&
          moment(a.date) < moment(this.enddate)
      );

      var hist = d3.bin().thresholds(20);
      var optional = this.timeArray
        .filter(item => this.valuetype.includes(item.type))
        .map(a =>
          this.$moment
            .duration(this.$moment(a.date).format("HH:mm:ss"))
            .asMinutes()
        );

      var graphoptional = [];
      var binsheight = hist(optional);
      var catheight = [];
      binsheight.forEach(bin => {
        graphoptional.push(bin.length);
        catheight.push(Math.round(bin["x0"] / 60));
      });

      this.chartOptionsBarTimeFrame.xAxis = {
        data: catheight
      };

      this.chartOptionsBarTimeFrame.series = [
        {
          type: "bar",
          data: graphoptional,
          barWidth: 20
        }
      ];
      this.makeUserGraph();
    },

    manipulateData() {
      this.makeGraph();
      this.pickerDate();
      this.makeUserGraph();
    },

    makeUserGraph() {
      var hist = d3.bin();
      var optional = this.results
        .filter(item => this.valuetype.includes(item.type))
        .map(a => a.user.userId);

      var graphUser = [];
      var catuser = [];

      var counts = {};

      for (var i = 0; i < optional.length; i++) {
        var num = optional[i];
        counts[num] = counts[num] ? counts[num] + 1 : 1;
      }

      for (var property in counts) {
        if (!counts.hasOwnProperty(property)) {
          continue;
        }

        catuser.push(property);
        graphUser.push(counts[property]);
      }

      this.chartOptionsBarUser.yAxis = {
        type: "category",
        data: catuser
      };

      this.chartOptionsBarUser.series = [
        {
          type: "bar",
          data: graphUser,
          name: "# of measures"
        }
      ];
    },

    makeGraph() {
      var xaxis = [
        "0",
        "1am",
        "2am",
        "3am",
        "4am",
        "5am",
        "6am",
        "7am",
        "8am",
        "9am",
        "10am",
        "12am",
        "1pm",
        "2pm",
        "3pm",
        "4pm",
        "5pm",
        "6pm",
        "7pm",
        "8pm"
      ];

      var hist = d3.bin().thresholds(20);

      this.test = this.results
        .filter(item => item.type == "Weight")
        .map(a =>
          this.$moment
            .duration(this.$moment(a.date).format("HH:mm:ss"))
            .asMinutes()
        );

      var bins = hist(this.test);

      var cat = [];
      bins.forEach(bin => {
        this.graph.push(bin.length);
        cat.push(Math.round(bin["x0"] / 60));
      });

      var height = this.results
        .filter(item => item.type == "Height")
        .map(a =>
          this.$moment
            .duration(this.$moment(a.date).format("HH:mm:ss"))
            .asMinutes()
        );

      var binsheight = hist(height);
      var catheight = [];
      binsheight.forEach(bin => {
        this.graphHeight.push(bin.length);
        catheight.push(Math.round(bin["x0"] / 60));
      });

      this.chartOptionsBarHeight.xAxis = {
        data: catheight
      };

      this.chartOptionsBar.xAxis = {
        data: cat
      };

      this.chartOptionsBarHeight.series = [
        {
          type: "bar",
          data: this.graphHeight,
          barWidth: 20
        }
      ];

      this.chartOptionsBar.series = [
        {
          type: "bar",
          data: this.graph,
          barWidth: 20
        }
      ];
    }
  }
};
</script>

<style>
</style>