 <template>
  <div id="patientBox" class="pa-2">
    <div>
      <v-dialog v-model="dialog" scrollable max-width="300px">
        <v-card>
          <v-card-title>Patient Details</v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <v-text-field label="Id" v-model="id" filled disabled></v-text-field>

            <v-text-field v-model="firstname" filled label="First Name"></v-text-field>
            <v-text-field label="Last Name" v-model="lastname" filled></v-text-field>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialog = false">Close</v-btn>
            <v-btn color="blue darken-1" text @click="savePatientDetails()">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>

    <v-card :flat="true" class="px-2" elevation="0.5">
      <v-layout column>
        <v-flex align-self-start>
          <v-card-title Device>
            <div class="title font-weight-bold">Patient</div>
            <div class="title font-weight-light pl-1">- details</div>
          </v-card-title>

          <v-card-subtitle Device>ID: {{patient.patientId}}</v-card-subtitle>
          <v-card-text>Name: {{patient.firstName}} {{patient.lastName}}</v-card-text>
        </v-flex>
        <v-divider class="ma-3"></v-divider>

        <v-layout row justify-space-between>
          <v-sheet>
            <div>
              <chart :options="chartOptionsBarWeight"></chart>
            </div>
          </v-sheet>
          <v-sheet>
            <div>
              <chart :options="chartOptionsBarHeight"></chart>
            </div>
          </v-sheet>
        </v-layout>
      </v-layout>
    </v-card>

    <b-table
      ref="table"
      :sticky-header="true"
      :items="allPatients"
      :fields="headers"
      @row-clicked="showBundle"
      @row-hovered="showPatientDetails"
      @row-dblclicked="showPatientDetailsDialog"
      :class="rowClass"
      head-variant="light"
      responsive="sm"
      sort-icon-left
      :per-page="perPage"
      :current-page="currentPage"
      :filter="filter"
      :filterIncludedFields="filterOn"
    ></b-table>
    <b-form-input v-model="filter" type="search" id="filterInput" placeholder="Type to Search"></b-form-input>
    <b-pagination
      v-model="currentPage"
      :total-rows="rows"
      :per-page="perPage"
      aria-controls="my-table"
      align="fill"
    ></b-pagination>

    <p class="mt-3">Current Page: {{ currentPage }}</p>
  </div>
</template>
 
<script>
const axios = require("axios").default;

export default {
  data() {
    return {
      value: [],
      patients: [],
      allPatients: [],
      perPage: 6,
      currentPage: 1,
      dialog: false,

      filter: null,
      filterOn: [],
      firstname: "",
      lastname: "",
      id: "",
      internal_id: "",
      currentIndex: "",

      chartOptionsBarHeight: {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross"
          }
        },

        xAxis: {
          type: "time",
          axisTick: {
            alignWithLabel: true
          }
        },
        yAxis: {
          type: "value",
          axisLabel: {
            formatter: "{value} m"
          }
        },
        series: [
          {
            type: "line",
            data: []
          }
        ],

        title: {
          text: "Height [m]",
          x: "center",
          textStyle: {
            fontSize: 18
          }
        },
        color: ["#127ac2"]
      },

      chartOptionsBarWeight: {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross"
          }
        },

        xAxis: {
          type: "time",
          axisTick: {
            alignWithLabel: true
          }
        },
        yAxis: {
          type: "value",
          axisLabel: {
            formatter: "{value} kg"
          }
        },
        series: [
          {
            type: "line",
            data: []
          }
        ],

        title: {
          text: "Weight [kg]",
          x: "center",
          textStyle: {
            fontSize: 18
          }
        },
        color: ["#127ac2"]
      },

      headers: [
        {
          label: "ID",
          key: "patientId",
          sortable: true
        },
        { label: "First Name", key: "firstName", sortable: true },
        { label: "Last Name", key: "lastName", sortable: true }
      ],
      patient: []
    };
  },

  beforeRouteEnter(to, from, next) {
    axios.get("http://"+window.location.host+"/measure/patients").then(response => {
      next(vm => (vm.allPatients = response.data));
    });
  },

  watch: {
    allPatients: "manipulateData"
  },

  computed: {
    rows() {
      return this.allPatients.length;
    }
  },

  methods: {
    savePatientDetails(item, index) {
      let currentObj = this;
      axios
        .post(
          "http://"+window.location.host+"/measure/patient/" +
            this.internal_id +
            "/update",
          {
            firstName: this.firstname,
            lastName: this.lastname
          }
        )
        .then(function(response) {
          currentObj.output = response.data;
          if (response.data == "OK") {
            currentObj.dialog = false;

            currentObj.allPatients[currentObj.currentIndex].firstname =
              currentObj.firstname;
            currentObj.allPatients[currentObj.currentIndex].lastname =
              currentObj.lastname;

            axios
              .get("http://"+window.location.host+"/measure/patients")
              .then(response => (currentObj.allPatients = response.data));
          }
        })
        .catch(function(error) {
          currentObj.output = error;
          currentObj.dialog = false;
        });
    },

    showPatientDetailsDialog(item, index) {
      this.lastname = item.lastName;
      this.firstname = item.firstName;
      this.id = item.patientId;
      this.internal_id = item._id;
      this.currentIndex = index;
      this.dialog = true;
    },

    manipulateData() {
      this.$refs.table.refresh();
      this.showBundle(this.allPatients[1]);
    },

    showPatientDetails(item, index) {
      this.patient = item;
    },
    rowClass(item) {
      if (item.patientId == "987") {
        console.log(show);
        return "highlight";
      } else {
        return "highlightUnSelected";
      }
    },
    showBundle(item) {
      console.log("in show bundle: " + item._id);
      console.log("in show bundle: " + item.patientId);
      axios
        .get("http://"+window.location.host+"/measure/measurement/" + item._id)
        .then(response => {
          this.chartOptionsBarWeight.series = [
            {
              type: "line",
              data: response.data
                .filter(item => item.type == "Weight")
                .map(a => [a.date, a.value])
            }
          ];

          this.chartOptionsBarHeight.series = [
            {
              type: "line",
              data: response.data
                .filter(item => item.type == "Height")
                .map(a => [a.date, a.value])
            }
          ];
        })
        .catch(function(error) {
          console.log(error);
        });
    }

    // Bar chart Measurmeents per day
    // Measurments per user
    // Accumulated weight per scale
    // Accumulated total weight
    //

    //this.bundleId = item.measurement_bundle_id;
    //this.$store.commit("setSelectedBundle", item.measurement_bundle_id);
  }
};
</script>

<style scoped>
.table-class {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  border-collapse: collapse;
  border-spacing: 1;
}

.highlight {
  border-right: 3px rgb(192, 187, 187, 0.5) solid;
  border-left: 5px solid #4caf50;
  background-color: rgb(192, 187, 187, 0.05);
}

.highlightUnSelected {
  border-left: 5px solid rgb(255, 255, 255);
}

table tbody {
  border-right: 5px solid white;
}

.head {
  padding: 0.5em;
  position: relative;
  border-bottom: 3px rgb(192, 187, 187, 0.2) solid;
  flex-grow: 0;
}

td {
  padding: 1em;
  position: relative;
  border-bottom: 1px rgb(192, 187, 187, 0.2) solid;
  flex-grow: 0;
}

td::after {
  content: "";
  background-color: rgb(192, 187, 187, 0.2);
  position: absolute;
  left: 100%;
  top: 6px;
  bottom: 6px;
  width: 1px;
}

td:last-child::after {
  border-right: 2px #ffffff solid;
}

th::after {
  content: "";
  background-color: rgba(26, 26, 26, 0.2);
  position: absolute;
  left: 100%;
  top: 6px;
  bottom: 6px;
  width: 1px;
}

th:last-child::after {
  border-right: 2px #ffffff solid;
}
</style>